import React from 'react';
import './Movie.css'
import {Link} from "react-router-dom";

const Movie = ( props : { id:number, year:number, title:string, poster:string, summary:string, genres:[string] }) => {


    return (
        <div className="movie">
            <Link to={{
                pathname:`/detail/${props.id}`,
                state: {
                    props
                }
            }}>
            <img src={props.poster} alt={props.title}/>
            <div className="movie__data">
                <h3 className="movie__title">{props.title}</h3>
                <h5 className="movie__year">{props.year}</h5>
                <ul className="movie__genres">
                {props.genres.map( (genres:string, index:number) => {
                    return <li className="genres__genre" key={index}>{genres}</li>
                })}
                </ul>
                <p className="movie__summary">{props.summary.slice(0, 180)}...</p>
            </div>
            </Link>
        </div>

    );
};

export default Movie;